html, body, #root {
  height: 100%;
}

body {
  font-family: 'Graphik', sans-serif;
  letter-spacing: -1px;
}

.wrap-app-grid {
  height: 100%;
  display: grid;
  grid-template-rows: 50px 1fr 50px;
}

.wrap-header {
  display: flex;
  justify-content: space-between;
  padding: 0 20px;
  align-items: center;
}

.logo-txt {
  font-size: 16px;
  font-weight: 500;
}

.wrap-header-li {
  display: flex;
}

.wrap-header-li li {
  margin-left: 14px;
  list-style: none;
}

.wrap-main-txt {
  padding: 20px;
}

.copy-txt {
  font-size: 14px;
  opacity: 0.6;
  margin-right: 20px;
  text-align: right;
}

.hello-txt {
  font-size: 28px;
  font-weight: 600;
  margin: 2px 0;
}

.main-txt {
  font-size: 24px;
  font-weight: 500;
  margin: 30px 0 10px 0;
  line-height: 30px;
}

.wrap-gall {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-row-gap: 20px;
  grid-column-gap: 20px;
  margin: 20px;
}


.wrap-gallary {
  border: 1px solid #eee;
  padding: 20px;
  margin: 2px;
}

.wrap-gallary h3 {
  margin: 0;
  display: inline-block;
}

.wrap-gallary .sub-title {
  margin: 2px 0 0 0;
  color: rgba(0, 0, 0, 0.5);
  display: inline-block;
  margin-left: 10px;
}

.wrap-gallary .wrok-type {
  margin: 5px 0 0 0;
  display: inline-block;
  color: rgba(0, 0, 0, 0.6);
}

.wrap-gallary .work-skill {
  margin: 5px 0 0 10px;
  display: inline-block;
  color: rgba(0, 0, 0, 0.6);
  font-weight: 700;
}

.wrap-gallary .sub-work {
  margin: 0 0 10px 0;
}

.wrap-gal-img {
  height: 300px;
  background: #eee;
}

footer {
  border-top: 1px solid #eee;
}